import { PieSeriesOption, XAXisComponentOption } from 'echarts';

type IAxisLabel = Required<XAXisComponentOption>
export enum AxisType {
  VALUE = 'value',
  CATEGORY = 'category',
  TIME = 'time',
}
export enum LegendIcon {
  CIRCLE = 'circle',
  RECT = 'rect',
  ROUNDRECT = 'roundRect',
  TRIANGLE = 'triangle',
  DIAMON = 'diamond',
  PIN = 'pin',
  ARROW = 'arrow',
  NONE = 'none'
}
export interface ThemeAxis {
  /**
   * 
   */
  name?: string;
  /**
   *  15
   */
  nameGap?: number;
  /**
   *      true
   */
  enable?: boolean;
  /**
   *   cecece
   */
  lineColor?: string;
  /**
   *    nameColor
   */
  tickTextColor?: string;
  /**
   * 
   * https://echarts.apache.org/zh/option.html#xAxis.axisLabel.formatter
   */
  tickTextFormatter?: IAxisLabel['axisLabel']['formatter'];
  /**
   *  grid 
   */
  gridLineEnable?: boolean;
  /**
   * grid   ececec
   */
  gridLineColor?: string;
  /**
   *  value  category  time   category
   */
  type?: AxisType;
  /**
   *   5
   */
  tickLength?: number;
  /**
   *    nameFontSize
   */
  tickFontSize?: number;
  /**
   *   cecece
   */
  tickLineColor?: string;
  /**
   *   b0b0b0
   */
  nameColor?: string;
  /**
   *  12
   */
  nameFontSize?: number;
}

export interface ThemeLegend {
  /**
   *   false
   */
  enable?: boolean;
  /**
   * 
   */
  fontSize?: number;
  /**
   * 
   */
  color?: string;
  /**
   * 
   */
  itemHeight?: number;
  /**
   * icon
   * https://echarts.apache.org/zh/option.html#legend.icon
   */
  icon?: LegendIcon | string;
}
type IFormat =
  | ((
      params: Object | Array<any>,
      ticket: string,
      callback: Function,
    ) => string | HTMLElement | HTMLElement[])
  | string;
interface Position {
  top?: number;
  left?: number;
  bottom?: number;
  right?: number;
}
export enum Trigger {
  ITEM = 'item',
  AXIS = 'axis',
  NONE = 'none'
}
export enum PointerType {
  LINE = 'line',
  SHADOW = 'shadow',
  CROSS = 'cross',
  NONE = 'none'
}
export interface ThemeTooltip {
  /**
   *  item   axis    item
   */
  trigger?: Trigger;
  /**
   *   shadow  line
   * https://echarts.apache.org/zh/option.html#tooltip.axisPointer.type
   */
  axisPointerType?: PointerType;
  /**
   * 
   * https://echarts.apache.org/zh/option.html#tooltip.formatter
   */
  formatter?: IFormat;
}
export interface Pie {
  /**
   *   false
   */
  circle?: boolean;
  /**
   * 
   * https://echarts.apache.org/zh/option.html#series-pie.itemStyle
   */
  circleItem?: PieSeriesOption['itemStyle'];

}
export interface Theme {
  yAxis?: ThemeAxis;
  xAxis?: ThemeAxis;
  legend?: ThemeLegend;
  padding?: Position;
  tooltip?: ThemeTooltip;
  pie?: Pie;
  /**
   *    false
   */
  smooth?: boolean;
  /**
   *        false
   */
  stack?: boolean;
  /**
   *    true
   */
  connectNulls?: boolean;
  /**
   *   true
   */
  labelShow?: boolean;
}
export enum ChartType {
  BAR = 'bar',
  LINE = 'line',
  SCATTER = 'scatter',
  AREA = 'area',
  PIE = 'pie'
}

interface TextStyle {
  color?: string;
  fontWeight?: string;
  fontSize?: number;
}
interface Title extends Position {
  text?: string;
  subtext?: string;
  show?: boolean;
  textStyle?: TextStyle;
}
type IDataSource = {
  [key: string]: string|number
}
export interface ChartProps {
  /**
   * 
   */
  height: number;
  dataSource: IDataSource[];
  /**
   * 
   */
  click?: Function;
  /**
   *  xfield(Xkey)*yfield(Ykey)  xfield yfield
   */
  position: string;
  /**
   *  
   */
  group?: string;
  theme?: Theme;
  /**
   * bar  line  area  scatter  pie 
   */
  chartType: ChartType;
  title?: Title;
}
export interface UseSeries
  extends Pick<ChartProps, 'dataSource' | 'group' | 'position' | 'chartType'>,
    Pick<
      Theme,
      'smooth' | 'stack' | 'connectNulls' | 'pie' | 'labelShow' | 'padding'
    > {
  lengendData?: string[];
}

export interface UseSeriesData extends Omit<UseSeries, 'position'> {
  xField?: string;
  yField?: string;
}

export interface UseLegendProps
  extends ThemeLegend,
    Pick<ChartProps, 'position' | 'chartType'> {
  group: string;
  dataSource: ChartProps['dataSource'];
  enable?: boolean;
}

export interface UseAxis
  extends Pick<
    ThemeAxis,
    'name' | 'enable' | 'tickTextColor' | 'gridLineEnable' | 'gridLineColor' | 'type'
  > {
  labelColor?: string;
  labelFontSize?: number;
  grindLineWidth?: string;
}
